import React from "react"
import { List } from "semantic-ui-react"

const PasswordReqs = () =>
  <List bulleted>
    <List.Item
      content="Vhintn 8 merkki"
    />
    <List.Item
      content="Vhintn yksi iso kirjain A-Z"
    />
    <List.Item
      content="Vhintn yksi pieni kirjain a-z"
    />
    <List.Item
      content="Vhintn yksi numero 0-9"
    />
    <List.Item
      content="Sallitut erikoismerkit: ! # $ % &amp; ' * + . : , ; / = ? @ ^ _ ~ -"
    />
  </List>

export default PasswordReqs
