import React from "react"
import { List } from "semantic-ui-react"

const EmptyList = () =>
  <List.Item disabled>
    <List.Content align="center" >
      <List.Header content="( Ei osumia! )" />
      <List.Description
        content="Joko lista on tyhj, tai hakuehtoja vastaavia merkintj ei lydy."
      />
    </List.Content>
  </List.Item>

export default EmptyList
